<script setup lang="ts">
const { t } = useI18n()
const router = useRouter()
const { $api } = useNuxtApp()

const auth = (typeof useAuth === 'function' ? useAuth() : null) as any
const user = computed(() => auth?.user?.value || null)
const clientToken = process.client ? localStorage.getItem('auth_token') : null

type TabKey = 'overview' | 'orders' | 'lookups'
const activeTab = ref<TabKey>('overview')

function setTab(tab: TabKey) {
  activeTab.value = tab
}

/* ---------------- Redirect if not logged in ---------------- */
onMounted(() => {
  if (!user.value && !clientToken) {
    router.push('/login?redirect=/account')
  } else {
    fetchOverview()
    fetchOrders()
    fetchLookups()
  }
})

/* ---------------- Overview / profile / wallets ---------------- */
const loadingOverview = ref(false)
const overviewError = ref<string | null>(null)
const profile = ref<any>(null)
const stats = ref<any>(null)
const wallets = ref<any[]>([])

// computed Kia wallets (generic – based on slug)
const kiaBeforeTokens = computed(() => {
  const w = wallets.value.find(
    (w: any) => w.service_slug === 'kia-hyundai-before-2017'
  )
  return w ? Number(w.tokens || 0) : 0
})

const kiaAfterTokens = computed(() => {
  const w = wallets.value.find(
    (w: any) => w.service_slug === 'kia-hyundai-after-2017'
  )
  return w ? Number(w.tokens || 0) : 0
})

// edit profile
const editingProfile = ref(false)
const savingProfile = ref(false)
const profileError = ref<string | null>(null)
const profileSuccess = ref<string | null>(null)

const profileForm = reactive({
  name: '',
  email: '',
  phone: '',
})

async function fetchOverview() {
  loadingOverview.value = true
  overviewError.value = null
  try {
    const res: any = await $api('/account/overview', { method: 'GET' })
    profile.value = res.profile || null
    stats.value = res.stats || null
    wallets.value = Array.isArray(res.wallets) ? res.wallets : []

    if (profile.value) {
      profileForm.name = profile.value.name || ''
      profileForm.email = profile.value.email || ''
      profileForm.phone = profile.value.phone || ''
    }
  } catch (e: any) {
    console.error(e)
    overviewError.value =
      e?.data?.message || e?.message || 'Failed to load overview'
  } finally {
    loadingOverview.value = false
  }
}

async function saveProfile() {
  profileError.value = null
  profileSuccess.value = null

  if (!profileForm.name || !profileForm.email) {
    profileError.value = 'Name and email are required.'
    return
  }

  savingProfile.value = true
  try {
    const res: any = await $api('/account/profile', {
      method: 'PUT',
      body: {
        name: profileForm.name,
        email: profileForm.email,
        phone: profileForm.phone || null,
      },
    })

    // update local state
    profile.value = res.user || {
      ...(profile.value || {}),
      name: profileForm.name,
      email: profileForm.email,
      phone: profileForm.phone,
    }

    profileSuccess.value =
      (t('account.profile.updated') as string) ||
      'Profile updated successfully.'
    editingProfile.value = false

    // optionally refresh auth user
    if (auth && typeof auth.fetchUser === 'function') {
      await auth.fetchUser()
    }
  } catch (e: any) {
    console.error(e)
    profileError.value =
      e?.data?.message ||
      e?.message ||
      ((t('account.profile.updateFailed') as string) ||
        'Failed to update profile.')
  } finally {
    savingProfile.value = false
  }
}

/* ---------------- Orders ---------------- */
const loadingOrders = ref(false)
const ordersError = ref<string | null>(null)
const orders = ref<any[]>([])
const ordersMeta = ref<any>({ current_page: 1, last_page: 1 })

async function fetchOrders(page = 1) {
  loadingOrders.value = true
  ordersError.value = null
  try {
    const res: any = await $api(`/account/orders?page=${page}`, {
      method: 'GET',
    })
    orders.value = Array.isArray(res.data) ? res.data : []
    ordersMeta.value = res.meta || { current_page: 1, last_page: 1 }
  } catch (e: any) {
    console.error(e)
    ordersError.value =
      e?.data?.message || e?.message || 'Failed to load orders'
  } finally {
    loadingOrders.value = false
  }
}

/* ---------------- Lookups ---------------- */
const loadingLookups = ref(false)
const lookupsError = ref<string | null>(null)
const lookups = ref<any[]>([])
const lookupsMeta = ref<any>({ current_page: 1, last_page: 1 })

async function fetchLookups(page = 1) {
  loadingLookups.value = true
  lookupsError.value = null
  try {
    const res: any = await $api(`/account/lookups?page=${page}`, {
      method: 'GET',
    })
    lookups.value = Array.isArray(res.data) ? res.data : []
    lookupsMeta.value = res.meta || { current_page: 1, last_page: 1 }
  } catch (e: any) {
    console.error(e)
    lookupsError.value =
      e?.data?.message || e?.message || 'Failed to load VIN lookups'
  } finally {
    loadingLookups.value = false
  }
}

/* ---------------- Helpers ---------------- */
const formatDate = (value: string | null) => {
  if (!value) return ''
  return new Date(value).toLocaleString()
}

const formatMoney = (value: number | string) => {
  return '$' + Number(value || 0).toFixed(2)
}
</script>

<template>
  <div class="max-w-6xl mx-auto px-4 py-8">
    <h1 class="text-2xl sm:text-3xl font-bold mb-6">
      {{ t('account.title') || 'My account' }}
    </h1>

    <!-- Tabs header -->
    <div class="border-b mb-4">
      <nav class="-mb-px flex gap-4 text-sm">
        <button
          type="button"
          class="border-b-2 pb-2 px-1"
          :class="
            activeTab === 'overview'
              ? 'border-orange-500 text-orange-600 font-semibold'
              : 'border-transparent text-gray-500 hover:text-gray-800'
          "
          @click="setTab('overview')"
        >
          {{ t('account.tabs.overview') || 'Overview' }}
        </button>

        <button
          type="button"
          class="border-b-2 pb-2 px-1"
          :class="
            activeTab === 'orders'
              ? 'border-orange-500 text-orange-600 font-semibold'
              : 'border-transparent text-gray-500 hover:text-gray-800'
          "
          @click="setTab('orders')"
        >
          {{ t('account.tabs.orders') || 'Orders' }}
        </button>

        <button
          type="button"
          class="border-b-2 pb-2 px-1"
          :class="
            activeTab === 'lookups'
              ? 'border-orange-500 text-orange-600 font-semibold'
              : 'border-transparent text-gray-500 hover:text-gray-800'
          "
          @click="setTab('lookups')"
        >
          {{ t('account.tabs.lookups') || 'VIN lookups' }}
        </button>
      </nav>
    </div>

    <!-- OVERVIEW TAB -->
    <section v-if="activeTab === 'overview'">
      <div
        v-if="overviewError"
        class="rounded-xl bg-red-50 text-red-700 px-3 py-2 mb-3 text-sm"
      >
        {{ overviewError }}
      </div>

      <!-- Main stats -->
      <div class="grid gap-4 md:grid-cols-3 mb-5">
        <div class="rounded-2xl border bg-white p-4">
          <div class="text-xs text-gray-500 mb-1">
            {{ t('account.cards.totalOrders') || 'Total orders' }}
          </div>
          <div class="text-2xl font-bold">
            {{ stats?.total_orders ?? 0 }}
          </div>
        </div>
        <div class="rounded-2xl border bg-white p-4">
          <div class="text-xs text-gray-500 mb-1">
            {{ t('account.cards.totalLookups') || 'VIN lookups' }}
          </div>
          <div class="text-2xl font-bold">
            {{ stats?.total_lookups ?? 0 }}
          </div>
          <div class="text-xs text-gray-500 mt-1">
            {{ t('account.cards.completed') || 'Completed' }}:
            {{ stats?.completed_lookups ?? 0 }}
          </div>
        </div>
        <div class="rounded-2xl border bg-white p-4">
          <div class="text-xs text-gray-500 mb-1">
            {{ t('account.cards.tokensSpent') || 'Tokens spent' }}
          </div>
          <div class="text-2xl font-bold">
            {{ stats?.total_tokens_spent ?? 0 }}
          </div>
        </div>
      </div>

      <!-- Kia / Hyundai token balances (from wallets) -->
      <div class="grid gap-4 md:grid-cols-2 mb-5">
        <div class="rounded-2xl border bg-white p-4">
          <div class="text-xs text-gray-500 mb-1">
            {{ t('account.cards.kiaBefore') || 'Kia / Hyundai tokens (before 2017)' }}
          </div>
          <div class="text-2xl font-bold">
            {{ kiaBeforeTokens }}
          </div>
        </div>

        <div class="rounded-2xl border bg-white p-4">
          <div class="text-xs text-gray-500 mb-1">
            {{ t('account.cards.kiaAfter') || 'Kia / Hyundai tokens (2017 and after)' }}
          </div>
          <div class="text-2xl font-bold">
            {{ kiaAfterTokens }}
          </div>
        </div>
      </div>

      <div class="grid gap-4 md:grid-cols-[2fr_3fr]">
        <!-- Profile card (editable) -->
        <div class="rounded-2xl border bg-white p-4">
          <div class="flex items-center justify-between mb-3">
            <h2 class="text-sm font-semibold">
              {{ t('account.profile.title') || 'Profile' }}
            </h2>
            <button
              type="button"
              class="text-xs font-medium text-orange-600 hover:text-orange-700"
              @click="
                () => {
                  editingProfile = !editingProfile
                  profileError = null
                  profileSuccess = null
                }
              "
            >
              {{
                editingProfile
                  ? t('common.cancel') || 'Cancel'
                  : t('account.profile.edit') || 'Edit'
              }}
            </button>
          </div>

          <!-- Read-only view -->
          <div v-if="!editingProfile" class="space-y-2 text-sm">
            <div>
              <div class="text-gray-500">
                {{ t('common.name') || 'Name' }}
              </div>
              <div class="font-medium">
                {{ profile?.name || '-' }}
              </div>
            </div>
            <div>
              <div class="text-gray-500">
                {{ t('common.email') || 'Email' }}
              </div>
              <div class="font-medium">
                {{ profile?.email || '-' }}
              </div>
            </div>
            <div>
              <div class="text-gray-500">
                {{ t('common.phone') || 'Phone' }}
              </div>
              <div class="font-medium">
                {{ profile?.phone || '-' }}
              </div>
            </div>
            <div>
              <div class="text-gray-500">
                {{ t('account.profile.memberSince') || 'Member since' }}
              </div>
              <div class="font-medium">
                {{ profile?.created_at ? formatDate(profile.created_at) : '-' }}
              </div>
            </div>
          </div>

          <!-- Edit form -->
          <form
            v-else
            class="space-y-3 text-sm"
            @submit.prevent="saveProfile"
          >
            <div>
              <label class="block text-xs text-gray-600 mb-1">
                {{ t('common.name') || 'Name' }}
              </label>
              <input
                v-model.trim="profileForm.name"
                type="text"
                class="w-full rounded-xl border px-3 py-2 focus:outline-none focus:ring-2 focus:ring-orange-400"
                required
              />
            </div>
            <div>
              <label class="block text-xs text-gray-600 mb-1">
                {{ t('common.email') || 'Email' }}
              </label>
              <input
                v-model.trim="profileForm.email"
                type="email"
                class="w-full rounded-xl border px-3 py-2 focus:outline-none focus:ring-2 focus:ring-orange-400"
                required
              />
            </div>
            <div>
              <label class="block text-xs text-gray-600 mb-1">
                {{ t('common.phone') || 'Phone' }}
              </label>
              <input
                v-model.trim="profileForm.phone"
                type="text"
                class="w-full rounded-xl border px-3 py-2 focus:outline-none focus:ring-2 focus:ring-orange-400"
              />
            </div>

            <div v-if="profileError" class="text-xs text-red-600">
              {{ profileError }}
            </div>
            <div v-if="profileSuccess" class="text-xs text-emerald-600">
              {{ profileSuccess }}
            </div>

            <button
              type="submit"
              class="inline-flex items-center justify-center rounded-xl bg-orange-500 text-white px-4 py-2 text-xs font-medium hover:bg-orange-600 transition disabled:opacity-60 disabled:cursor-not-allowed"
              :disabled="savingProfile"
            >
              <span v-if="!savingProfile">
                {{ t('common.save') || 'Save' }}
              </span>
              <span v-else>
                {{ t('common.loading') || 'Saving…' }}
              </span>
            </button>
          </form>
        </div>

        <!-- Wallets -->
        <div class="rounded-2xl border bg-white p-4">
          <h2 class="text-sm font-semibold mb-3">
            {{ t('account.wallets.title') || 'Service wallets' }}
          </h2>

          <div v-if="!wallets.length" class="text-xs text-gray-500">
            {{ t('account.wallets.empty') || 'No wallets yet.' }}
          </div>

          <ul v-else class="divide-y text-sm">
            <li
              v-for="w in wallets"
              :key="w.id"
              class="flex items-center justify-between py-2"
            >
              <div>
                <div class="font-medium">
                  {{ w.service_name || ('Service #' + w.service_id) }}
                </div>
                <div class="text-xs text-gray-500">
                  {{ t('account.wallets.tokens') || 'Tokens' }}
                </div>
              </div>
              <div class="text-lg font-bold">
                {{ w.tokens }}
              </div>
            </li>
          </ul>
        </div>
      </div>
    </section>

    <!-- ORDERS TAB -->
    <section v-else-if="activeTab === 'orders'">
      <div
        v-if="ordersError"
        class="rounded-xl bg-red-50 text-red-700 px-3 py-2 mb-3 text-sm"
      >
        {{ ordersError }}
      </div>

      <div class="rounded-2xl border bg-white overflow-hidden w-full">
        <table class="w-full min-w-full border-collapse text-sm">
          <thead class="bg-gray-50 text-xs uppercase text-gray-500">
            <tr>
              <th class="px-6 py-2 text-left">#</th>
              <th class="px-6 py-2 text-left">
                {{ t('account.orders.date') || 'Date' }}
              </th>
              <th class="px-6 py-2 text-left">
                {{ t('account.orders.status') || 'Status' }}
              </th>
              <th class="px-6 py-2 text-left">
                {{ t('account.orders.payment') || 'Payment' }}
              </th>
              <th class="px-6 py-2 text-right">
                {{ t('account.orders.total') || 'Total' }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="!orders.length">
              <td colspan="5" class="px-6 py-4 text-center text-gray-500">
                {{ t('account.orders.empty') || 'No orders yet.' }}
              </td>
            </tr>
            <tr
              v-for="o in orders"
              :key="o.id"
              class="border-t"
            >
              <td class="px-6 py-2">#{{ o.id }}</td>
              <td class="px-6 py-2">{{ formatDate(o.created_at) }}</td>
              <td class="px-6 py-2 capitalize">{{ o.status }}</td>
              <td class="px-6 py-2 capitalize">{{ o.payment_method }}</td>
              <td class="px-6 py-2 text-right font-medium">
                {{ formatMoney(o.subtotal) }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- Simple pagination -->
      <div
        v-if="ordersMeta.last_page > 1"
        class="flex items-center justify-end gap-2 mt-3 text-xs"
      >
        <button
          type="button"
          class="px-2 py-1 rounded border"
          :disabled="ordersMeta.current_page <= 1"
          @click="fetchOrders(ordersMeta.current_page - 1)"
        >
          ‹
        </button>
        <span>
          {{ ordersMeta.current_page }} / {{ ordersMeta.last_page }}
        </span>
        <button
          type="button"
          class="px-2 py-1 rounded border"
          :disabled="ordersMeta.current_page >= ordersMeta.last_page"
          @click="fetchOrders(ordersMeta.current_page + 1)"
        >
          ›
        </button>
      </div>
    </section>

    <!-- LOOKUPS TAB -->
    <section v-else-if="activeTab === 'lookups'">
      <div
        v-if="lookupsError"
        class="rounded-xl bg-red-50 text-red-700 px-3 py-2 mb-3 text-sm"
      >
        {{ lookupsError }}
      </div>

      <div class="rounded-2xl border bg-white overflow-hidden w-full">
        <table class="w-full min-w-full border-collapse text-sm">
          <thead class="bg-gray-50 text-xs uppercase text-gray-500">
            <tr>
              <th class="px-6 py-2 text-left">#</th>
              <th class="px-6 py-2 text-left">
                {{ t('account.lookups.service') || 'Service' }}
              </th>
              <th class="px-6 py-2 text-left">
                {{ t('account.lookups.status') || 'Status' }}
              </th>
              <th class="px-6 py-2 text-right">
                {{ t('account.lookups.cost') || 'Tokens' }}
              </th>
              <th class="px-6 py-2 text-left">
                {{ t('account.lookups.date') || 'Date' }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="!lookups.length">
              <td colspan="5" class="px-6 py-4 text-center text-gray-500">
                {{ t('account.lookups.empty') || 'No VIN lookups yet.' }}
              </td>
            </tr>
            <tr
              v-for="l in lookups"
              :key="l.id"
              class="border-t"
            >
              <td class="px-6 py-2">#{{ l.id }}</td>
              <td class="px-6 py-2">
                {{ l.service_name || ('Service #' + l.service_id) }}
              </td>
              <td class="px-6 py-2 capitalize">
                {{ l.status }}
              </td>
              <td class="px-6 py-2 text-right font-medium">
                {{ l.cost_tokens }}
              </td>
              <td class="px-6 py-2">
                {{ formatDate(l.created_at) }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div
        v-if="lookupsMeta.last_page > 1"
        class="flex items-center justify-end gap-2 mt-3 text-xs"
      >
        <button
          type="button"
          class="px-2 py-1 rounded border"
          :disabled="lookupsMeta.current_page <= 1"
          @click="fetchLookups(lookupsMeta.current_page - 1)"
        >
          ‹
        </button>
        <span>
          {{ lookupsMeta.current_page }} / {{ lookupsMeta.last_page }}
        </span>
        <button
          type="button"
          class="px-2 py-1 rounded border"
          :disabled="lookupsMeta.current_page >= lookupsMeta.last_page"
          @click="fetchLookups(lookupsMeta.current_page + 1)"
        >
          ›
        </button>
      </div>
    </section>
  </div>
</template>

<style scoped>
.tabular-nums {
  font-variant-numeric: tabular-nums;
}
</style>
